  max-width: 200px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.table-styling {
  margin-right: 5px;
  margin-left: -5px;
}
.set-row-height{
  height: 85px !important;
}
.overflow-td{
  overflow-y: auto;
  width: 150px;
